/* ************************
   02.2: Buttons
   ********************* */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 500;
  color: #ffffff;
  padding: 21.5px 32px;
  line-height: 1;
  letter-spacing: 1.2px;
  white-space: nowrap;
  text-transform: uppercase;
  border-radius: 50px;
}

.btn:hover {
  color: #ffffff;
}

.btn.btn-borderd {
  background-color: transparent;
  border: 1px solid #ffffff;
}

.btn-play {
  height: 74px;
  width: 74px;
  border-radius: 50%;
  background-color: #ffffff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-play:after {
  border: 8px solid rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  content: '';
  display: block;
  position: absolute;
  left: -8px;
  top: -8px;
  width: calc(100% + 16px);
  height: calc(100% + 16px);
  z-index: -1;
}

.btn-play:hover:after {
  animation: ripple 1s linear infinite;
}

.btn-icon {
  font-size: 14px;
  font-weight: 700;
}

.btn-icon .icon {
  height: 22px;
  width: 22px;
  border-radius: 5px;
  margin-right: 5px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@keyframes ripple {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
    border-color: transparent;
  }
}

@-webkit-keyframes ripple {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(2);
    transform: scale(2);
    border-color: transparent;
  }
}

