  overflow: hidden;
}

.theme-input-wrapper .theme-input-style, .theme-input-wrapper .coronaDataTableWrapper .dataTables_filter input, .coronaDataTableWrapper .dataTables_filter .theme-input-wrapper input {
  flex: 1 1 0%;
  min-width: 0;
  border: none;
  height: 60px;
  background-color: #ffffff;
